<template>
  <el-row class="warp" style="padding:20px 0 0 20px;">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
     <el-form :inline="true">
      <el-form-item>
        <el-button type="primary" size="medium" @click="newone"></el-button>
      </el-form-item>
      <el-form-item>
        <el-input v-model="filter.name" placeholder="///" style="min-width: 260px;" ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="medium" @click="newone"></el-button>
        <el-button size="medium" @click="newone"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="list" v-loading="loading" border stripe size="small">
      <el-table-column prop="name" label="" min-width="80" align="center">
      </el-table-column>
      <el-table-column prop="url" label="" min-width="200" align="center">
        <template slot-scope="scope">
          <img :src="scope.row.url" style="max-width:100px;max-height:100px;" />
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" min-width="200" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="200" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="200" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150" align="center">
      </el-table-column>
      <el-table-column prop="name" label="/" min-width="150" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150" align="center">
      </el-table-column>
      <el-table-column label="" min-width="200" align="center">
       <template slot-scope="scope">
        <el-tooltip class="icon" effect="dark" content="" placement="top">
          <img src="../../../static/images/icon/edit.png" @click="handleEdit(scope.$index, scope.row)">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="bottom">
          <img src="../../../static/images/icon/look.png" @click="handleEdit(scope.$index, scope.row)">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="top">
          <img src="../../../static/images/icon/ewcode.png" @click="handleEdit(scope.$index, scope.row)">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="bottom">
          <img src="../../../static/images/icon/check.png" @click="handleEdit(scope.$index, scope.row)">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="top">
          <img src="../../../static/images/icon/delete.png" @click="handleDelete(scope.$index, scope.row)">
        </el-tooltip>
      </template>
    </el-table-column>
  </el-table>

  <el-pagination style="float:left;margin-top:20px;" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="limit" @current-change="handleCurrentChange" @size-change="handleSizeChange" layout="total,sizes, prev, pager, next, jumper" :total="count" prev-text="" next-text="">
  </el-pagination>
</el-col>

<el-col>
  <el-dialog :title="diatitle" :visible.sync="dialogNewVisible" width="700px" center style="min-width: 500px">
    <el-form label-width="120px" size="small" >

<!--       <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item> 

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="role_id">
        <el-select v-model="newadv.role_id" placeholder="">
          <el-option v-for="item in rolesArr" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="role_id">
        <el-select v-model="newadv.role_id" placeholder="">
          <el-option v-for="item in rolesArr" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item> -->

      <el-form-item label="">
        <el-upload class="upload-demo" :action="upurl" :data="uptoken" :before-upload="beforeUpload" :on-success="handleSuccess" :show-file-list="false" accept="image/*">
          <!-- <i class="el-icon-plus"></i> -->
          <img :src="imgSrc" class="pre-img" style="width:60%;height:30%;border:2px dashed #ccc;border-radius:10px;display: block" >
          <el-button size="small" type="primary" style="display: block;margin-top: 20px;"></el-button>
          <div slot="tip" class="el-upload__tip">JPG/PNG1M</div>
        </el-upload>
      </el-form-item>

      <el-form-item label="">
        <el-upload :action="upurl" :data="uptoken" list-type="picture-card" :on-remove="handleRemove" :on-success="handlelistSuccess" :on-exceed="handleExceed" :file-list="album" :limit="3">
          <i class="el-icon-plus"></i>
        </el-upload>
<!--         <el-dialog :visible.sync="dialogVisible1">
          <img width="100%" :src="dialogImageUrl1">
        </el-dialog> -->
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="role_id">
        <el-select v-model="newadv.role_id" placeholder="">
          <el-option v-for="item in rolesArr" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>

      <el-form-item style="margin-left: calc(50% - 185px);">
        <el-button type="primary" @click="save()"> </el-button>
        <el-button @click="dialogNewVisible = false"> </el-button>
      </el-form-item>

    </el-form>
  </el-dialog>
</el-col>


<el-col>
  <el-dialog title="" :visible.sync="dialogDelVisible" width="30%">
    <div slot="footer" class="dialog-footer">
      <el-button type="danger" @click="submitdel()"> </el-button>
      <el-button type="primary" @click="dialogDelVisible = false"> </el-button>
    </div>
  </el-dialog>
</el-col>

</el-row>
</template>



<script>


  import qiniu from '../../api/qiniu';

  export default {
    data() {
      return {
        uptoken:{
          token:qiniu.token,
        },
        upurl:qiniu.upurl,
        currentPage: 1,
        list:[{
          name:'11',
          url:'../static/images/gold.jpg'
        }],
        loading: false,
        count:100,
        limit:10,
        dialogNewVisible:false,
        dialogDelVisible:false,
        filter:{
          name:''
        },
        putorup:'up',
        imgSrc:"../static/images/default.png",
        newadv:{
          link:'',
          intro:'',
          positionid:'',
          album:'',
          role_id:''
        },
        rolesArr:[],
        diatitle:'',
        postarr:[],
        // rules: {
        //   link: [{required: true, trigger: 'blur',message: ''}]
        // },
        editId:'',
        delId:'',
        album:[]
      };
    },

    methods:{
      getlist(){

      },

      newone(){
       this.putorup='up';
       this.imgSrc="../static/images/default.png";
       this.diatitle='',
       this.dialogNewVisible=true
     },

     beforeUpload(file) {
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (!isLt1M) {
        this.$message.error(' 1MB!');
      }
      return isLt1M;

      
    },

    handleSuccess(res, file) {
      this.upimgurl =qiniu.showurl+ res.key
      this.imgSrc =qiniu.showurl+ res.key
    },

    save(){

      var piclist=[]
      for(var i = 0 ; i < this.album.length; i++){
        var aaa = qiniu.showurl+this.album[i].response.key;
        piclist.push(aaa)
      }
      console.log(piclist)

      this.dialogNewVisible=false
    },

    handleEdit(index, row){
      this.diatitle='';
      this.dialogNewVisible = true;
      this.putorup='put';
      this.editId = row.id;
      this.imgSrc=this.showUrl+row.url;
    },

    handleDelete(index, row) {
      this.dialogDelVisible = true;
      this.delId = row.id;
    },

    submitdel(){
      this.dialogDelVisible = false;
    },

    handleCurrentChange(val) {
      this.currentPage = val;
      this.getlist();
    },

    handleSizeChange(val){
      this.limit = val;
      this.getlist();
    },

    handleRemove(file, fileList) {
      this.album=fileList
    },
    
    handleExceed(files, fileList) {
      // this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      this.$message.warning(`3`);
    },

    handlelistSuccess(res, file,fileList){
      this.album=fileList
    },
  },

  mounted: function () {
    this.getlist();
  }
}
</script>


<style>
.icon{
  width: 20px;
  height: 20px;
  margin: 2px;
}
</style>


